/* 전체 스타일 */
body {
  font-family: 'Netflix Sans', Arial, sans-serif;
  background-color: #141414;
  color: #fff;
  margin: 0;
  padding: 0;
}

.Logo {
  display: flex;
  justify-content: left;
  align-items: center;
  margin-bottom: 10px;
}

.logo-image {
  width: 150px; /* 이미지 크기를 적절하게 조정 */
  height: auto;
}

/* 사용자 선택 페이지 스타일 */
.user-selection-page {
  margin-top: -140px;
  padding: 50px;
  font-family: 'Netflix Sans', Arial, sans-serif;
  background-color: #090808;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  text-align: center;
}

.user-selection-page h1 {
  font-size: 2.5rem;
  margin-bottom: 24px;
  color: #FFFFFF;
}

.user-selection-page p {
  font-size: 1.125rem;
  margin-bottom: 24px;
  color: #FFFFFF;
}

.user-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 40px; /* 수직 및 수평 간격 조정 */
  justify-content: center; /* 그리드 항목을 가운데 정렬 */
  align-items: center; /* 그리드 항목을 가운데 정렬 */
}

#addUserButton {
  justify-self: center; /* 버튼을 그리드 셀 안에서 가운데 정렬 */
  display: flex;
  justify-content: center;
  align-items: center;
}

.user-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  transition: transform 0.3s;
}

.user-card:hover {
  transform: scale(1.05);
}

.user-icon-cat {
  font-size: 6.25rem; /* 100px */
  color: #f12c64;
  margin-bottom: 10px;
}

.user-card p {
  font-size: 1.875rem; /* 30px */
  font-weight: bold;
  color: #f5f0f2;
}

.user-selection-page button {
  padding: 12px 24px;
  background-color: #f12c64;
  color: #080808;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
  margin: 16px 8px;
  font-size: 1rem;
  font-weight: bold;
}

.user-selection-page button:hover {
  background-color: #2396c3;
}

.user-selection-page input[type="text"],
.user-selection-page select {
  margin-bottom: 16px;
  padding: 12px;
  border: 1px solid #ffffff;
  border-radius: 4px;
  width: 100%;
  box-sizing: border-box;
  font-size: 1rem;
  background-color: #020202;
  color: #CCCCCC;
}

.user-selection-page label {
  display: block;
  margin-bottom: 8px;
  font-size: 1rem;
  color: #CCCCCC;
}

/* 회원가입 모달 스타일 */
.signup-modal {
  display: flex;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.8);
  justify-content: center;
  align-items: center;
}

.signup-modal-content {
  background-color: #141414;
  padding: 40px;
  border: 1px solid #f4f1f1;
  width: 80%;
  max-width: 500px;
  border-radius: 8px;
  position: relative;
}

.signup-modal-content h2 {
  font-size: 1.5rem;
  margin-bottom: 24px;
  color: #FFFFFF;
}

.signup-modal-content .close {
  color: #CCCCCC;
  position: absolute;
  top: 10px;
  right: 20px;
  font-size: 1.75rem;
  font-weight: bold;
  cursor: pointer;
}

.signup-modal-content .close:hover,
.signup-modal-content .close:focus {
  color: #FFFFFF;
  text-decoration: none;
}

/* 미디어 쿼리 */
@media (max-width: 1200px) {
  .user-selection-page h1 {
    font-size: 2.25rem;
  }

  .user-selection-page p {
    font-size: 1.125rem;
  }

  .user-card p {
    font-size: 1.75rem;
  }

  .user-icon-cat {
    font-size: 5.5rem;
  }

  .user-selection-page button {
    font-size: 0.875rem;
  }
}

@media (max-width: 992px) {
  .user-selection-page h1 {
    font-size: 2rem;
  }

  .user-selection-page p {
    font-size: 1rem;
  }

  .user-card p {
    font-size: 1.625rem;
  }

  .user-icon-cat {
    font-size: 5rem;
  }

  .user-selection-page button {
    font-size: 0.875rem;
  }

  .user-grid {
    gap: 30px; /* 중간 화면에서 간격 줄이기 */
  }
}

@media (max-width: 768px) {
  .user-selection-page {
    padding: 30px;
  }

  .user-selection-page h1 {
    font-size: 1.75rem;
  }

  .user-selection-page p {
    font-size: 0.875rem;
  }

  .user-card p {
    font-size: 1.5rem;
  }

  .user-icon-cat {
    font-size: 4.5rem;
  }

  .user-selection-page button {
    font-size: 0.875rem;
  }

  .user-grid {
    gap: 20px; /* 모바일 환경에서 간격 줄이기 */
  }
}

@media (max-width: 576px) {
  .user-selection-page {
    padding: 20px;
  }

  .user-selection-page h1 {
    font-size: 1.5rem;
  }

  .user-selection-page p {
    font-size: 0.75rem;
  }

  .user-card p {
    font-size: 1.25rem;
  }

  .user-icon-cat {
    font-size: 4rem;
  }

  .user-selection-page button {
    font-size: 0.75rem;
  }

  .user-grid {
    gap: 15px; /* 더 작은 화면에서 간격 줄이기 */
  }
}

@media (max-width: 480px) {
  .user-selection-page {
    padding: 10px;
  }

  .user-selection-page h1 {
    font-size: 1.25rem;
  }

  .user-selection-page p {
    font-size: 0.75rem;
  }

  .user-card p {
    font-size: 1rem;
  }

  .user-icon-cat {
    font-size: 3.5rem;
  }

  .user-selection-page button {
    font-size: 0.75rem;
  }

  .user-grid {
    gap: 10px; /* 더 작은 화면에서 간격 줄이기 */
  }
}
